.app {
    background-color: #f3f2ef;
    display: flex;
    flex-direction: column;
}
.app_body {
    display: flex;
    margin-top: 35px;
    max-width: 1200px;
    margin-left: 20px;
    margin-right: 20px;
}
.header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgray;
    padding: 10px 0px;
    width: 100%;
    background-color: white;
}

.header_left {
    display: flex;
}
.header_left > img {
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}

.header_search {
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height: 22px;
    color: gray;
    background-color: #eef3f8;
}

.header_search > input {
    outline: none;
    border: none;
    background: none;
}

.header_right {
    display: flex;
}
.headerOption {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    color: gray; 
    cursor: pointer;
}

.headerOption:hover {
    color: black;
}

.headerOption_title {
    font-size: 12px;
    font-weight: bold;
}

.headerOption_icon {
    object-fit: contain;
    height: 25px !important;
    width: 25px !important;
}
.sidebar {
    top: 80px;
    position: -webkit-sticky;
    position: sticky;
    flex: 0.2 1; 
    border-radius: 10px;
    text-align: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.sidebar_avatar {
    margin-bottom: 10px;
}

.sidebar_top > h4 {
    color: gray;
    font-size: 12px;
}

.sidebar_top > h2 {
    font-size: 18px;
}

.sidebar_top {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
}

.sidebar_top > img {
    margin-bottom: -10px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
}

.sidebar_stats {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.sidebar_stat {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.sidebar_stat > p {
    color: gray;
    font-size: 13px;
    font-weight: 600;
}

.sidebar_statNumber {
    font-weight: bold;
    color: #0a66c2 !important;
}

.sidebar_bottom {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
}

.sidebar_bottom > p {
    font-size: 13px;
    padding-bottom: 10px;
}

.sidebar_recentItem {
    display: flex;
    font-size: 13px;
    color: gray;
    font-weight: bolder;
    cursor: pointer;
    margin-bottom: 5px;
    padding: 5px;
}
.sidebar_hash {
    margin-right: 10px;
    margin-left: 5px;
}
.sidebar_recentItem:hover {
    background-color: whitesmoke;
    color: black;
    border-radius: 10px;
}
.feed {
    flex: 0.6 1;
    margin: 0 20px;
}

.feed_inputContainer {
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.feed_input {
    border: 1px solid lightgray;
    border-radius: 30px;
    display: flex;
    padding: 10px;
    color: gray;
    padding-left: 15px;
}

.feed_input > form {
    display: flex;
    width: 100%;
}

.feed_input > form > input {
    border: none;
    flex: 1 1;
    outline-width: 0;
    font-weight: 600;
    margin-left: 10px;
}

.feed_input > form > button {
    display: none;
}

.feed_InputOptions {
    display: flex;
    justify-content: space-evenly;
}
.InputOption {
    display: flex;
    align-items: center;
    margin-top: 15px;
    color: gray;
    padding: 10px;
    cursor: pointer;
}

.InputOption:hover {
    background-color: whitesmoke;
    border-radius: 10px;
}

.InputOption > h4 {
    margin-left: 5px;
}
.post {
    background-color: white;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
}

.post_header {
    display: flex;
    margin-bottom: 10px;
}

.post_info {
    margin-left: 10px;
}

.post_info > p {
    font-size: 12px;
    color: gray;
}

.post_body {
    overflow-wrap: anywhere;
}

.post_info > h2 {
    font-size: 15px;
}

.post_buttons {
    display: flex;
    padding-right: 5px;
}
.widgets {
    position: -webkit-sticky;
    position: sticky;
    top: 80px;
    flex: 0.2 1;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-bottom: 10px;   
}
.widgets_header {
    display: flex;
    padding: 10px;
    align-items: center;
    justify-content: space-between;
}
.widgets_header > h2 {
    font-size: 16px;
    font-weight: 400;
}

.widgets_article {
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widgets_article :hover {
    background-color: whitesmoke;
}

.widget_articleLeft {
    color: #0177b7;
    margin-right: 5px;
}

.widget_articleLeft > .MuiSvgIcon-root {
    font-size: 15px;
}

.widget_articleRight {
    flex: 1 1;
}

.widget_articleRight > h4 {
    font-size: 12px;
}

.widget_articleRight > p {
    font-size: 12px;
    color: gray;
}
.login {
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 100px;
}

.login > img {
   object-fit: contain;
   height: 70px;
   margin-top: 20px;
   margin-bottom: 20px;
}

.login > form {
    display: flex;
    flex-direction: column;
}

.login > form > input {
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.login > form > button {
    background-color: #0A66C2;
    width: 365px;
    border-radius: 15px;
    height: 50px;
    font-size: large;
    color: white;
}

.login_register {
    color: #0177b7;
    cursor: pointer;
    margin-left: 4px;
}

.login > p {
    margin-top: 20px;
}
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

