.login {
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
    padding-bottom: 100px;
}

.login > img {
   object-fit: contain;
   height: 70px;
   margin-top: 20px;
   margin-bottom: 20px;
}

.login > form {
    display: flex;
    flex-direction: column;
}

.login > form > input {
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.login > form > button {
    background-color: #0A66C2;
    width: 365px;
    border-radius: 15px;
    height: 50px;
    font-size: large;
    color: white;
}

.login_register {
    color: #0177b7;
    cursor: pointer;
    margin-left: 4px;
}

.login > p {
    margin-top: 20px;
}